:global {
	.fade-enter {
		opacity: 0;
	}
	.fade-enter-active {
		opacity: 1;
		transition: opacity 1s ease-in;
	}
	.fade-enter-done {
		opacity: 1;
	}
	.fade-exit {
		opacity: 1;
	}
	.fade-exit-active {
		opacity: 0;
		transition: opacity 1s ease-in;
	}
	.fade-exit-done {
		opacity: 0;
	}
}
.popoverContainer {
	background-color: #fff;
	border: 1px solid #d7d9db;
	border-radius: 2px;
	box-shadow: 0 1px 8px 0 rgb(0 0 0 / 20%);
	z-index: 10;
	padding: 12px 16px;
	position: absolute;
	box-sizing: border-box;
}

.popoverContent {
	// width: 100px;
}
.arrow {
	width: 10px;
	height: 10px;
	display: inline-block;
	border-right: 1px solid #d7d9db;
	border-bottom: 1px solid #d7d9db;
	background: #fff;
	transform: rotate(45deg);
	border-radius: 1px;
	position: absolute;
	top: 40px;
	z-index: 999;
}
