.container {
	width: 200px;
	// border: 1px solid #ccc;
	padding: 12px;
}

.row {
	display: flex;
	margin-bottom: 8px;
}
.item {
	flex-shrink: 0;
	flex-grow: 1;
	text-align: center;
	color: #aeafb5;
}

.dateItem {
	flex-shrink: 0;
	flex-grow: 1;
	text-align: center;
	color: rgb(15, 15, 15);
	padding: 1px;
	cursor: pointer;
	display: inline-block;
    font-size: 12px;
}

.minItem {
	padding: 0 2px;
}
.interactive {
	.minItem {
		&:hover {
			background-color: #e6f9eb;
			// color: #fff;
		}
	}
}

.header {
	display: flex;
	margin-bottom: 12px;
	.text {
		margin: 0 auto;
	}
	
}

.cursor {
    cursor: pointer;
    svg {
        &:hover {
            fill: #00b831;
        }
       
    }
}

.disable {
	background-color: #f0f0f5;
	color: #93959e;
	cursor: not-allowed;
	// pointer-events: none;
}

.border {
	.minItem {
		border: solid 1px #00b831;
	}
}

.noCurrentMonth {
	.minItem {
		color: #aeafb5;
	}
}
.selected {
	.minItem {
		color: #fff;
		background-color: #00b831;
		&:hover {
			background-color: #00b831;
			// color: #fff;
		}
	}
}
.input {
    width: 200px;
    height: 16px;
    padding: 8px;
    outline: none;
    border: 1px solid #ccc;
    border-radius: 2px;
    &:hover {
        border: 1px solid #00b831;
    }
   
}
    :global {
        .datePick.datePick {
            padding: 0;
        }
    }

.monthHeader {
    display: flex;
    justify-content: space-between;
    margin-bottom: 12px;
}

.between {
    background-color: #e6f8ea;
}
.rightBack {
    background: linear-gradient(
90deg
,#fff 0,#fff 0%,#e6f8ea 51%,#e6f8ea);
}
.leftBack {
    background: linear-gradient(
90deg
,#e6f8ea 0,#e6f8ea 76%,#fff 51%,#fff);

}